.typewriter {
    display: inline-block;
    font-family: 'Courier New', Courier;
}

.typewriter-text {

    display: inline-block;
  	overflow: hidden;
  	letter-spacing: 2px;
    -webkit-animation: typing 5s steps(50, end), blink .5s step-end infinite;
            animation: typing 5s steps(50, end), blink .5s step-end infinite;
    white-space: nowrap;
    font-size: 22px;
    font-weight: 700;
    border-right: 4px solid var(--primary);
    margin-top: 30px;
    margin-left: 30px;

}

@-webkit-keyframes typing {

    from {
        width: 0%;
    }
    to {
        width: 100%;
    }

}

@keyframes typing {

    from {
        width: 0%;
    }
    to {
        width: 100%;
    }

}

@-webkit-keyframes blink {
    from, to { 
        border-color: transparent;
    }
    50% { 
        border-color: var(--primary); 
    }
}

@keyframes blink {
    from, to { 
        border-color: transparent;
    }
    50% { 
        border-color: var(--primary); 
    }
}

